@media (min-width: 1200px) {
  * {
    text-decoration: none;
  }

  .home {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }

  /* HEADER////////////////////////////// */

  .header {
    width: 100%;
    display: flex;
    align-items: center;
    height: 60px;
    position: fixed;
    z-index: 100;
    justify-content: center;
    background: white;
    border-bottom: 2px solid rgba(227, 227, 227, 0.477);
  }

  .header-items {
    width: 90%;
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: space-between;
  }

  .text-1 {
    font-family: Verdana, Times, serif;
    color: rgb(8, 7, 8);
    cursor: pointer;
  }

  .text-1 span {
    color: rgb(55, 144, 255);
  }

  .div-2 {
    width: 35%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 30px;
  }

  .text-2 {
    color: rgb(55, 144, 255);
    font-weight: bold;
    font-family: Tahoma, Times, serif;
    transition: all 0.6s ease;
    border-bottom: 2px solid rgb(55, 144, 255);
  }

  .text-3 {
    font-family: Tahoma, Times, serif;
    color: gray;
  }

  .text-3:hover {
    border-bottom: 2px solid rgb(55, 144, 255);
  }

  .div-3 {
    display: flex;
    justify-content: space-between;
    width: 20%;
    align-items: center;
  }

  .img-1 {
    width: 30px;
    cursor: pointer;
  }

  .img-1:hover {
    opacity: 85%;
  }

  .img-1:active {
    opacity: 75%;
  }

  .img-2 {
    width: 30px;
    cursor: pointer;
    display: none;
  }

  .img-2:hover {
    opacity: 90%;
  }

  .btn-1 {
    color: rgb(55, 144, 255);
    margin-right: 10px;
  }

  .btn-1:hover {
    opacity: 90%;
  }

  .btn-2 {
    background-color: rgb(55, 144, 255);
    font-weight: bold;
    padding: 6px 20px;
    border-radius: 2px;
    color: white;
  }

  .btn-2:hover {
    opacity: 90%;
  }

  /* SECTION-1/////////////////////// */

  .section-1 {
    display: flex;
    width: 100%;
    height: 700px;
    margin-top: 80px;
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }

  .text-4 {
    font-family: Verdana, Times, serif;
    color: rgb(8, 7, 8);
    cursor: pointer;
    font-size: 50px;
  }

  .text-4 span {
    color: rgb(55, 144, 255);
  }
}

